  width: auto;
  border: 0 none;
  border-radius: 3px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: .4em 1em .2em;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transition: all .1s ease;
  transition: all .1s ease;
  background: #f0f0f0;
  outline: none !important;
}
button.yellow, .yellow {
  background: #ffd400;
  color: #000;
}
button.red, .red {
  background: #ff4040;
  color: #fff;
  font-weight: 700;
}
button.grey, .grey {
  background: #f0f0f0;
  color: #000;
}
.showBlock-enter-active, .showBlock-leave-active {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
  overflow: hidden;
}
.showBlock-leave, .showBlock-enter-to {
  max-height: 100vh;
  opacity: 1;
}
.showBlock-enter, .showBlock-leave-to {
  max-height: 0;
  opacity: 0;
}

